	import bannerSwiper from "@/components/mycom/swiper.vue"
	export default {
		components: {
			bannerSwiper
		},
		data() {
			return {
               imgList:[],
			   content:'',
			   id:''
			}
		},
		onLoad(options) {
           this.id=options.id;
		   this.loadData()
		},
		methods:{
			loadData(){
				let parmas={
					id:this.id
				}
				this._reqw.ipost(parmas,'workerDetails').then(res=>{
					res.result==0?(this.imgList=res.imgList,this.content=res.content):this.$api.tip(res.resultNote)
				}).catch(err=>{})
			}
		}
	}
